<template>
  <div id="color-animation">
    <div v-for="i in 6" :key="'animation-'+i" :style="{'animation-delay': (0.6+i*0.3)+'s'}"></div>
  </div>
</template>

<script>
export default {
  name: 'Animation'
}
</script>

<style>
#color-animation {
  width: auto;
  /* font-size: 0px; */
}

#color-animation > * {
  width: 10px;
  height: 5px;
  border: none;
  border-radius: 0;
  display: inline-block;
  vertical-align: middle;
  background-color: #9b59b6;
  margin: auto 0px;

  animation-name: likes;
  /* animation-direction: alternate; */
  animation-timing-function: linear;
  /* animation-delay: 0s; */
  animation-iteration-count: infinite;
  animation-duration: 2s;
}

@keyframes likes {
  0% {
    transform: scaleY(1);
    background-color: #9b59b6;
  }
  25% {
    transform: scaleY(7);
    background-color: #3697da;
  }
  50% {
    transform: scaleY(1);
    background-color: #9b59b6;
  }
  100% {
    transform: scaleY(1);
    background-color: #9b59b6;
  }
}
</style>



