<template>
  <svg :style="style" aria-hidden="true">
    <use :xlink:href="`#${name}`"></use>
  </svg>
</template>

<script>
export default {
  name: 'svg-icon',
  props: {
    name: {
      type: String,
      required: true
    },
    width: {
      type: String,
      default: '1em'
    },
    height: {
      type: String,
      default: '1em'
    }
  },
  data () {
    return {
      style: {
        width: this.width,
        height: this.height
      }
    }
  }
  // },
  // computed: {
  //   svgClass () {
  //     if (this.className) {
  //       return 'svg-icon ' + this.className
  //     } else {
  //       return 'svg-icon'
  //     }
  //   }
  // }
}
</script>

<style>
.svg-icon {
  fill: currentColor;
  overflow: hidden;
}
</style>



