<template>
  <aside id="aside">
    <div id="aside-user">
      <img src="https://s2.ax1x.com/2019/07/20/ZzawN9.jpg"/>
      <span>
        <strong>lightoffutrue</strong>
        <br>
        <span>动起来</span>
      </span>
    </div>
    <ul id="aside-ulist">
      <li class="aside-ulist-title">导航</li>
      <li v-for="(item,index) in mainList" :key="'main-'+index" class="nav">
        <a :href="`https://www.write-down.tech${item.path}`">
          <svg-icon :name="item.icon"/>
          <div>{{ item.text }}</div>
        </a>
      </li>
      <li class="aside-ulist-title">组成</li>
      <li v-for="(item,index) in secondaryList" :key="'secondary'+index" class="sub-nav" @click="getOpenLogo(index)">
        <svg-icon :name="item.icon"/>
        <div>{{ item.text }}</div>
        <svg-icon :name="item.state"/>
      </li>
    </ul>
    <aside-bottom/>
  </aside>
</template>

<script>
import AsideBottom from './AsideBottom'

export default {
  name: 'Aside',
  data: function () {
    return {
      mainList: [
        { path: '/', text: '首页', icon: 'Home' },
        { path: '/about.html', text: '关于', icon: 'InformationCircle' },
        { path: '/todo.html', text: 'Todo', icon: 'ClipboardList' },
        { path: '/error.html', text: '未解', icon: 'QuestionMarkCircle' },
        { path: '/navi.html', text: '导航', icon: 'Bookmark' },
        { path: '/author.html', text: '博主', icon: 'Users' },
        { path: '/admin/register.php', text: '注册', icon: 'Login' }
      ],
      opendSecondaryItem: -1,
      secondaryList: [
        {text: '分类', icon: 'ViewGrid', state: 'ChevronRight', data: []},
        {text: '页面', icon: 'Document', state: 'ChevronRight', data: []},
        {text: '友链', icon: 'UserCircle', state: 'ChevronRight', data: []}
      ]
    }
  },
  methods: {
    getOpenLogo (index) {
      if (this.opendSecondaryItem === index) {
        this.opendSecondaryItem = -1
        this.secondaryList[index].state = 'ChevronRight'
      } else {
        if (this.opendSecondaryItem !== -1) {
          this.secondaryList[this.opendSecondaryItem].state = 'ChevronRight'
        }
        this.opendSecondaryItem = index
        this.secondaryList[index].state = 'ChevronDown'
      }
    }
  },
  components: {
    'aside-bottom': AsideBottom
  }
}
</script>

<style scoped>
#aside {
  /* z-index: 10; */
  border-radius: 0px;
  box-sizing: border-box;
  padding: 15px 15px 150px;
  position: relative;
}

#aside-ulist {
  position: absolute;
  top: 100px;
  bottom: 50px;
  overflow: hidden;
  overflow-y: scroll;
}

#aside-ulist::-webkit-scrollbar {
  display: none;
}

.aside-ulist-title {
  background-color: transparent;
  font-size: small;
  margin: 20px auto;
}
.aside-ulist-title:hover {
  background-color: transparent;
}

.sub-nav, .nav {
  list-style-type: none;
  cursor: pointer;
  list-style-type: none;
  padding: 8px 16px;
}

.sub-nav > *, .nav > a > * {
  display: inline-block;
  vertical-align: middle;
  /* flex: 1; */
}

.sub-nav > div, .nav div {
  margin: 0 60px 0 25px;
}

#aside-user {
  box-sizing: border-box;
  height: 60px;
  margin: 20px 0 30px;
  white-space: nowrap;
}

#aside-user * {
  display: inline-block;
  vertical-align: middle;
}

#aside-user > img {
  width: 60px;
  height: 60px;
  border-radius: 30px;
  margin: auto 15px;
}
</style>



