<template>
  <div>
    <h5>{{ title }}</h5>
    <ul id="tab-data-items">
      <li v-for="(item,index) in tabData" :key="index">
        <a :href="`https://www.write-down.tech/${item.path}`">
          <img :src="item.image">
        </a>
        <a id="title" :href="`https://www.write-down.tech/${item.path}`"> {{ item.title }}</a>
        <small :class="{'with-icon': icon != null}">
          <svg-icon v-show="icon" name="ChatBubbleLeft" width="16px" height="16px"/>
          <div>{{ item.text }}</div>
        </small>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HotArticles',
  props: {
    title: {
      type: String,
      requred: true
    },
    tabData: {
      type: Array,
      requred: true
    },
    icon: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style scoped>

ul {
  list-style: none;
}

li {
  margin: 6px 6px;
  padding: 6px 10px;
  display: grid;
  grid-row-gap: 6px;
  grid-column-gap: 8px;
  grid-template-columns: 36px 5fr;
  /* grid-template-rows: 1fr 1fr; */
}

li > a {
  grid-column: 1;
  grid-row: 1 / 3;
  display: flex;
  align-items: center;
}

img {
  border-radius: 6px;
  width: 36px;
}

#title {
  font-size: 13px;
  grid-column: 2;
  grid-row: 1;
}

small {
  font-size: 9px;
  grid-column: 2;
  grid-row: 2;
  display: flex;
}

small.with-icon > div {
  padding: 0px 10px;
}
</style>



