<template>
  <div id="content">
    <router-view/>
    <aside>
      <section>
        <ul @click="entrust" class="status-list">
          <li v-for="(item,index) in handles" :key="index" :data-index="index">
            <svg-icon :name="item.icon" width="18px" height="18px"/>
            <div :class="current == index ? 'status checked' : 'status unchecked'"></div>
          </li>
        </ul>
        <son-tab v-for="(item,index) in handles" :key="'handles-'+index"
        :title="item.title" :icon="item.svg" :tabData=item.data v-show="current == index"/>
      </section>
      <section>
        <blog-info title="博客信息" :blogInfo=blogInfo></blog-info>
      </section>
      <section>
        <h5>标签云</h5>
        <div class="tags" v-for="(item,key) in tags" :key="'tag-'+key">{{ item }}</div>
      </section>
    </aside>
  </div>
</template>

<script>
import SonTab from './SonTab'
import BlogInfo from './BlogInfo'

export default {
  name: 'Content',
  data () {
    return {
      current: 0,
      handles: [
        {
          icon: 'HandThumbUp',
          svg: true,
          title: '热门文章',
          data: [
            {
              'path': '/junior/tabletop-ornament.html',
              'image': 'https://www.write-down.tech/usr/themes/handsome/assets/img/sj2/5.jpg',
              'title': '【ESP8266】桌面摆件',
              'text': '0'
            },
            {
              'path': '/sophomore/Git-Bash.html',
              'image': 'https://www.write-down.tech/usr/themes/handsome/assets/img/sj2/1.jpg',
              'title': '【工具】Git Bash使用',
              'text': '0'
            }
          ]
        },
        {
          icon: 'ChatBubbleLeftRight',
          title: '最新评论',
          svg: false,
          data: [
            {
              'path': '/cross.html#comment-213',
              'image': 'https://q.qlogo.cn/g?b=qq&nk=2454271718&s=100',
              'title': 'Y夜',
              'text': '考完了！总共10门课平均分95！！！'
            }
          ]
        },
        {
          icon: 'Gift',
          title: '随机文章',
          svg: true,
          data: [
            {
              'path': '/sophomore/Git-Bash.html',
              'image': 'https://www.write-down.tech/usr/themes/handsome/assets/img/sj2/1.jpg',
              'title': '【工具】Git Bash使用',
              'text': '0'
            },
            {
              'path': '/junior/tabletop-ornament.html',
              'image': 'https://www.write-down.tech/usr/themes/handsome/assets/img/sj2/5.jpg',
              'title': '【ESP8266】桌面摆件',
              'text': '0'
            }
          ]
        }
      ],
      blogInfo: {
        '文章数目': {
          icon: 'CodeBracketSquare',
          text: '102'
        },
        '评论数目': {
          icon: 'ChatBubbleLeft',
          text: '129'
        },
        '运行天数': {
          icon: 'CalendarDays',
          text: '2年84天'
        },
        '最后活动': {
          icon: 'Sparkles',
          text: '4天前'
        }
      },
      tags: [
        'C',
        '未完成',
        '算法',
        '计算机基础',
        '练习',
        '游戏',
        'C++',
        '数据结构',
        '数据库',
        '计算机组成原理',
        '找工作',
        '指针',
        'Esp8266',
        'Arduino',
        'HTML',
        '工具',
        '库函数',
        'Linux',
        'go',
        '视频剪辑',
        '高等数学',
        '引用',
        'Apache',
        'Typecho',
        'times2',
        '产品经理',
        '人工智能',
        '项目实践',
        '密码学',
        '线程'
      ]
    }
  },
  methods: {
    getCurrentContent () {
      return this.handles[this.current].title
    },
    entrust (e) {
      let dom = e.target
      while (dom.parentElement !== e.currentTarget) {
        dom = dom.parentElement
      }
      let index = dom.getAttribute('data-index')
      if (index !== null) {
        this.current = index
      }
    }
  },
  components: {
    'son-tab': SonTab,
    'blog-info': BlogInfo
  }
}
</script>

<style scoped>
#content {
  min-width: 600px;
  max-width: 900px;
  display: flex;
}

aside {
  /* border-radius: 6px; */
  padding: 10px 10px;
  width: 240px;
}

div.status {
  margin: 6px 0px;
  width: 100%;
  height: 2px;
  border-radius: 1px;
}

ul.status-list {
  list-style: none;
  display: table;
}

ul.status-list > li{
  display: table-cell;
  list-style-type: none;
  padding: 5px  25px;
  cursor: pointer;
}

.tags {
  display: inline-block;
  padding: 3px 7px;
  margin: 4px;
}
</style>



