<template>
  <div id="all-layout">
    <blog-header/>
    <blog-aside/>
    <div id="sub-layout">
      <blog-content/>
      <blog-footer/>
    </div>
  </div>
</template>

<script>
import Aside from '@/components/Aside'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import Content from '@/components/Content'

export default {
  name: 'App',
  components: {
    'blog-aside': Aside,
    'blog-header': Header,
    'blog-footer': Footer,
    'blog-content': Content
  }
}
</script>

<style>
body {
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}

#all-layout {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: grid;
  grid-gap: 0;
  grid-row: 40px auto;
  grid-column: 200px auto;
  justify-content: center;
}

#header {
  grid-row: 1;
  grid-column: 1 / 3;
  height: 40px;
  padding: 5px;
  display: flex;
}

#aside {
  grid-row: 2 / 4;
  grid-column: 1;
  overflow: hidden;
  /* margin-top: 0px; */
  /* height: 100%; */
}

#sub-layout {
  grid-row: 2 / 4;
  grid-column: 2;
  overflow: hidden;
  overflow-y: scroll;
}

#content {
  margin-top: 0px;
}

</style>



