<template>
  <div>
    <h5>{{ title }}</h5>
    <ul>
      <li v-for="(item,key) in blogInfo" :key="key">
        <svg-icon :name="item.icon"></svg-icon>
        <span>{{ key }}</span>
        <div class="tags">{{ item.text }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'BlogInfo',
  props: {
    title: {
      type: String,
      requred: true
    },
    blogInfo: {
      type: Object,
      requred: true
    }
  }
}
</script>

<style scoped>
ul {
  padding: auto;
}

li {
  position: relative;
  padding: 10px 10px;
  display: flex;
  align-items: center;
  font-size: 14px;
}

li > *:first-child {
  display: inline-block;
  padding-left: 4px;
  padding-right: 20px;
}

li > *:last-child {
  position: absolute;
  margin-right: 10px;
  right: 0;
}
</style>



