<template>
  <header id="header" class=".app-header">
    <div id="app-home-logo">
      <a href="https://www.write-down.tech/">
        <span id="app-logo-time">
          <svg-icon name="AcademicCap" width="20px" height="20px"></svg-icon>
          <span>08: 00</span>
        </span>
      </a>
    </div>
    <div id="app-calendar">
      <svg-icon name="Clock" width="20px" height="20px"/>
      <svg-icon name="ChevronDoubleDown" width="15px" height="15px"/>
    </div>
    <div id="app-search">
      <form id="app-serch-form">
        <input autocomplete="off" type="search"  required="" placeholder="输入关键词搜索…">
        <button type="submit">
          <svg-icon name="Search" width="20px" height="20px"/>
        </button>
      </form>
    </div>
    <div id="app-right-bar">
      <Animaion/>
      <svg-icon name="Moon" width="20px" height="20px" class="button"></svg-icon>
      <svg-icon name="ChatBubbleLeftEllipsis" width="20px" height="20px" class="button"></svg-icon>
      <svg-icon name="ArrowLeftOnRectangle" width="20px" height="20px" class="button"></svg-icon>
    </div>
  </header>
</template>

<script>
import Animaion from './Animation'
export default {
  name: 'Header',
  components: {
    Animaion
  }
}
</script>

<style scoped>

#header {
  position: relative;
  border-radius: 0;
}

#header > * {
  padding: 8px;
}

#app-logo-time > * {
  display: inline-block;
  vertical-align: middle;
}

#app-calendar {
  margin-left: 80px;
}

#app-calendar > * {
  display: inline-block;
  vertical-align: middle;
}

#app-search {
  margin-left: 40px;
}

#app-home-logo {
  margin-left: 20px;
}

#app-right-bar {
  display: flex;
  position: absolute;
  box-sizing: border-box;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

#app-right-bar > * {
  display: inline-block;
  padding: 4px 20px;
  vertical-align: middle;
  cursor: pointer;
}

/* #app-serch-form {
  padding: 8px;
} */

#app-serch-form > * {
  display: inline-block;
  vertical-align: middle;
  height: 30px;
}

input {
  box-sizing: border-box;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  padding-left: 16px;
  width: 160px;
  color: #aaa;
  border: none;
  margin-right: -3px;
  outline: 2px solid transparent;
  background-color: transparent;
}

input:focus {
  outline: 2px solid #293b58;
}

button {
  color: #aaa;
  cursor: pointer;
  width: 45px;
  border: none;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  padding-right: 15px;
  background-color: #191919;
}

</style>



