<template>
  <ul id="aside-bottom">
    <li v-for="(item, index) in buttons" :key="index">
      <a :href="`https://www.write-down.tech${item.path}`">
        <svg-icon :name="item.icon" width="20px" height="20px"/>
        <div>{{ item.text }}</div>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'AsideBottom',
  data () {
    return {
      buttons: [
        {text: '管理', icon: 'Cog', path: '/admin/', tip: '后台管理'},
        {text: '文章', icon: 'Wifi', path: '/feed/', tip: '文章RSS地址'},
        {text: '评论', icon: 'ChatBubbleOvalLeft', path: '/feed/comments/', tip: '评论RSS地址'}
      ]
    }
  }
}
</script>

<style scoped>
ul {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  display: flex;
  height: 50px;
  transform: translateX(-50%);
}

li {
  flex: 1;
  padding: 6px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

a {
  padding: 0;
  margin: 0;
}

div {
  padding-top: 1px;
  padding-bottom: 2px;
  font-size: 11px;
}
</style>



